export default ".comingSoon{\n  font-size: 30px;\n  margin-top: 50px;\n  text-align: center;\n}\n\n/**\n  CSS FOR TOOLBAR\n  **/\n\n.matCard {\n  background-color: #e2e2e2;\n}\n\n.matCardButtons button:hover{\n  color: rgb(232, 235, 84);\n}\n\n.matHeading {\n  color: rgb(7, 7, 7);\n  margin-right: 20px;\n}\n\n.right-way {\n        color: rgb(226, 202, 62);\n        font-size: 25px;\n        font-family: 'Playfair Display', serif;\n        text-align: center;\n  }\n\n/**\n  CSS FOR BODY OF HOME COMPONENT\n  **/\n\n.homePage{\n    /* background-color: black; */\n    padding-left: 50px;\n    padding-right: 50px;\n    font-family: \"Montserrat\", arial, tahoma;\n    /* margin-top: 50px; */\n  }\n\n.homeMatCard {\n    margin-top: 25px;\n    padding: 0px !important;\n  }\n\n/**\n  CSS FOR IMAGE AND HOVER IMAGE EFFECT\n  **/\n\n.img{\n    width: 100%;\n    height: 350px;\n\t  transform: scale(1);\n   transition: .3s ease-in-out;\n  }\n\n.img:hover {\n    transform: scale(1.3);\n      opacity: 1;\n      cursor: pointer;\n  }\n\n.imgOverflow{\n    overflow: hidden;\n  }\n\n/**\n  CSS FOR TEXT BOX SHOW ON HOVER IMAGE\n  **/\n\n.hoverTextbox {\n    position: absolute;\n    bottom: -40px;\n    display: block;\n    text-align: center;\n    background-color: rgb(48, 49, 47);\n    color: white;\n    height: 125px;\n    margin-left: 10px ;\n    width: 150px !important;\n    font-family: \"Montserrat\";\n    font-size: 12px;\n    transition: .3s ease-in-out;\n    opacity: 0;\n  }\n\n.img:hover+.hoverTextbox {\n    bottom: 200px;\n    opacity: 0.8;\n  }\n\n/**\n  CSS FOR TEXT BELOW THE IMAGES\n  **/\n\n.middle{\n    margin-right: 20%;\n    margin-left: 20%;\n    margin-bottom: 20px !important;\n    text-align: center;\n  }\n\n.text{\n    margin-top: 20%;\n    cursor: pointer;\n\t  transition: .3s ease-in-out;\n  }\n\n.text:hover{\n    margin-top: 20%;\n    font-weight: bold;\n    text-decoration: underline;\n  }\n\n.paragraph{\n    font-size: 12px;\n    line-height: 16px;\n  }\n\n.label{\n    font-size: 16px;\n    cursor: pointer;\n    text-decoration: underline;\n\t  transition: .3s ease-in-out;\n  }\n\n.label:hover{\n    font-weight: bold;\n    text-decoration: none;\n  }\n\n/**\n  CSS FOR IMAGE SLIDER ANIMATION\n  **/\n\n.imgSlider{\n    -webkit-animation: myfirst 3s linear infinite;\n            animation: myfirst 3s linear infinite;\n  }\n\n@-webkit-keyframes myfirst {\n      0% {\n          transform: scale(1,1);\n      }\n      80% {\n          transform: scale(1.2,1.2);\n      }\n      100% {\n          transform: scale(1,1);\n      }\n  }\n\n@keyframes myfirst {\n      0% {\n          transform: scale(1,1);\n      }\n      80% {\n          transform: scale(1.2,1.2);\n      }\n      100% {\n          transform: scale(1,1);\n      }\n  }\n\n.imgSlider:hover {\n      opacity: 1;\n  }\n\n.sliderMatCard{\n    padding: 0px;\n    overflow: hidden;\n  }\n\n.sliderText{\n    position: absolute;\n\t  bottom: 0px;\n    display: block;\n    text-align: center;\n    background-color: rgb(48, 49, 47);\n    color: white;\n    height: 60px;\n    /* margin-left: 10px ; */\n    width: 100% !important;\n    font-family: \"Montserrat\";\n\t  font-size: 12px;\n\t  /* -webkit-transition: .3s ease-in-out;\n\t  transition: .3s ease-in-out; */\n\t  opacity: 0.7;\n  }\n\n@media(max-width:600px){\n\n  .homeMatCard{\n    /* margin-top: 25px; */\n    padding: 0px !important;\n    /* overflow: hidden; */\n  }\n  .imgOverflow{\n    overflow: hidden;\n  }\n  .homePage{\n    /* background-color: black; */\n    padding-left: 20px;\n    padding-right: 20px;\n    /* margin-top: 50px; */\n  }\n  .img{\n    width: 100%;\n    height: 550px;\n  }\n  .img:hover{\n    /* opacity: 0.8; */\n    cursor: pointer;\n  }\n}\n\n@media(max-width:450px){\n  .img{\n    width: 100%;\n    height: 420px;\n  }\n  .img:hover+.hoverTextbox {\n    bottom: 180px;\n    opacity: 0.8;\n  }\n}\n\n@media(min-width:992px){\n  .img{\n    width: 100%;\n    height: 400px;\n  }\n  .img:hover+.hoverTextbox {\n    bottom: 180px;\n    opacity: 0.8;\n  }\n}\n\n/* input color class */\n\n/* ::ng-deep input.mat-input-element {\n    background-color: #fff !important;\n  } */\n\n/* Change label color on focused */\n\n::ng-deep .mat-form-field.mat-focused .mat-form-field-label {\n    color: #fff !important;\n  }\n\n/* underline border color on focused */\n\n::ng-deep .mat-focused .mat-form-field-underline .mat-form-field-ripple{\n    background-color: #fff !important;\n  }\n/*# sourceMappingURL=data:application/json;base64, */"